
*{
  font-family: Arial, sans-serif;
}
.App{
  display: flex;
  flex-direction: row;

}
.App .halfPage {
  padding: 10px;
  width: 50%;
  position: relative;
  align-items: center;
  height: 100vh;
  display: flex;
}
.form {
  background-color: whitesmoke;
}
.container-inner{
  width: 100%;
}
.export{
  position: absolute;
  padding: 10px;
  bottom: 0px;
  left: 0px;
  width: 100%;
  display: flex;
  flex-direction: row-reverse;


}

.export button {
  margin-right: 20px;
  margin-top: 10px;

}
.export button:last-child{
  margin-right: 0px;
}
.custom-file-label{
  text-align: left;
}
.custom-file-label::after{
  content: "Выбрать" !important;
}
.form-control{
  margin-top: 10px;
}

.previewer-wrapper{
  width: 141px;
  margin: auto;
  height: 188px;
}
